@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400;500;700&display=swap);
html{background-color:#ebecfc;margin:0;font-family:Roboto, sans-serif;color:#51417e}h1,h2,h3{text-transform:uppercase}.App{margin:30px;display:grid;justify-content:center}.container{width:70%;margin:auto}

.postInfo{padding:0 20px;border:solid 1px #51417e;border-radius:15px;margin-bottom:20px;background-color:white}.postInfo__article{display:flex;flex-direction:column}.postInfo__article-author{align-self:flex-end}

.commentInfo{display:grid;grid-template-columns:20% 80%;padding:10px;border-radius:15px;background-color:white}.commentInfo:not(:last-of-type){margin-bottom:10px}.commentInfo__avatar{height:80px;margin-left:20px}.commentInfo__email{color:#c27e7e;font-size:10px;margin-left:20px}

.commentList{padding:10px;border-radius:15px;margin-bottom:20px;background-color:#ebecfc}

.userInfo{font-size:12px;padding:10px}

